import React, { useContext, useState } from "react";

import { GameContext } from "./Game";
import styles from "./Experience.module.scss";
import ProgressBar from "./ProgressBar";

const Experience = () => {
  // const [percentagfe]
  const { answerHistory } = useContext(GameContext);

  const exps = [
    {
      word: "bayabas",
      meaning: "guava",
      correct: 3,
      next: 10
    },
    {
      word: "masarap",
      meaning: "delicious",
      correct: 9,
      next: 10
    },
    {
      word: "dagat",
      meaning: "sea",
      correct: 1,
      next: 10
    },
    {
      word: "kaliwa",
      meaning: "left",
      correct: 10,
      next: 10
    }
  ];

  const expResult = exps.map(exp => {
    const current = (exp.correct / exp.next) * 100;
    const [percentage, setPercentage] = useState(current);

    return (
      <div className={styles.row} key={exp.word}>
        <div className={styles.columnWord}>
          <p className={styles.word}>{exp.word}</p>
          <p className={styles.meaning}>{exp.meaning}</p>
        </div>
        <div className={styles.columnResult}>
          <ProgressBar percentage={percentage} />
          <span className={styles.current}>{percentage} %</span>
        </div>
      </div>
    );
  });

  return (
    <div className={styles.wrapper}>
      <div>{expResult}</div>
    </div>
  );
};

export default Experience;
