import React, { useContext } from "react";

import GameStart from "./GameStart";
import GameOngoing from "./GameOngoing";
import GameResult from "./GameResult";

import { GameContext } from "./Game";
import styles from "./Main.module.scss";

const renderMain = () => {
  const { isStarted, isDefeated, isFinished, quizzes } = useContext(
    GameContext
  );

  if (!isStarted) {
    return <GameStart />;
  }

  if (quizzes.length === 0) {
    return (
      <React.Fragment>
        <div>...On Loading</div>
      </React.Fragment>
    );
  }

  return isDefeated || isFinished ? <GameResult /> : <GameOngoing />;
};

export default () => (
  <div className={styles.container}>
    <div className={styles.main}>{renderMain()}</div>
  </div>
);
